
#basic {
  width: 655px;
  float: left;
  margin-left: 40px;
  margin-top: 3px;
}
#basic .greenbutton{
  width: 77px;
  margin: 0 auto;
  margin-top: 35px;
  margin-bottom: 48px;
}
#basic .basic-content{
  width: 655px;
  height: 190px;
  margin-top: 34px;
  margin-bottom: 5px;
}
#basic .basic-content div{
  float: left;
}
#basic .basic-content .left{
  width: 63px;
}
#basic .basic-content .middle{
  width: 162px;
  position:relative;
  margin-left: 13px;
}
#basic .basic-content .middle .show{
  visibility: hidden;
  height: 22px;
}
#basic .basic-content .middle .wrapper{
  width:53px;
  height:53px;
  margin-top:8px;
}
#basic .basic-content .middle .wrapper p{  
  line-height: 53px;
}
#basic .basic-content .right .wrapper{
  width: 256px;
  height: 144px;
  margin-top:12px;
}
#basic .basic-content .right .wrapper p{
  line-height: 147px;
  font-size: 25px;
}
#basic .basic-content .middle .el-form-item__error{
  width: 162px;
  line-height: 20px;
  height: 20px;
}
#basic .basic-content button{
  float:left;
  margin-top: 23px;
  width: 74px;
  height: 32px;
  background-color: #fff;
  border: 1px solid #cfcfcf;
  color: #cfcfcf;
  border-radius: 3px;
  font-size: 12px;
  outline:none;
}
#basic .basic-content .buttonfocus{
  border: 1px solid #0175d8;
  color: #0175d8;
}
#basic .verticalline{
  position:absolute;
  background-color:#ccc;
  height:32px;
  width:1px;
  left:85px;
}
#basic .basic-content .el-select .el-input__inner{
  width: 120px;
  height: 32px;
  color: #38393b;
  font-size: 13px;
  border-color: #cccccc;
}
#basic .basic-content .el-select .el-input .el-input__icon{
  color: #363a3d;
}
#basic .basic-content .right{
  width: 320px;
  margin-left: 37px;
}
#basic .showtype{
  width: 655px;
  height: 36px;
  margin-bottom: 25px;
  margin-top: 20px;
}
#basic .showtype .left{
  float: left;
}
#basic .showtype .left>span{
  margin-left: 3px;
  line-height: 32px;
  margin-right: 15px;
}
#basic .showtype .el-button .el-button--default{
  border-color: #ccc;
  height: 36px;
}
#basic .showtype .el-button span{
  color: #ccc;
  font-size: 12px;
}
#basic .showtype .el-button:hover{
  border-color: #0175da;
}
#basic .showtype .buttonfocus{
  background-color: #0175da;
}
#basic .showtype .buttonfocus span{
  color: #fff;
}
#basic .showtype .right{
  float: right;
}
#basic a{
  float: right;
  margin-right: -470px;
  line-height: 36px;
  font-size: 11px;
  color: #898989;
}
.showtype .el-input__inner{
  width: 382px;
  height: 36px;
  border-color: #d9d9d9;
}
#basic .showtype .el-input__inner:hover{
  border-color: #99A9BF;
}
#basic .showtype .el-input__inner:focus{
  border-color: #99A9BF;
}
#basic .tips span{
  float: left;
}



