
.menubar{
  width: 222px;
  float: left;
  margin-left: 36px;
}
.menubar p{
  line-height: 40px;
  margin-bottom: 10px;
  font-size: 18px;
  color: #000;
}
.menubar .menu-content ul{
  list-style: none;
  
  overflow: hidden;
  border-radius: 5px; 
  border-top: 1px solid #eee;
}
.menubar .menu-content a{
  display: block;
  height: 40px; 
  width: 220px;
  background-color: #fff;
  text-decoration: none;
  text-indent: 17px;
  line-height: 40px;
  font-size: 13px;

  border: 1px solid #eee;
  margin-top: -1px;
}
.menubar .menu-content .router-link-active{
  background-color: #ff710f;
  color: #fff;
  border: 1px solid #ff710f;
}



