
#activity{
  width: 680px;
  float: left;
  margin-left: 40px;
  margin-top: 3px;
  margin-bottom: 40px;
}
#activity .act-num{
  margin-bottom: 7px;
}
#activity .act-num span{
  line-height: 40px;
}
#activity .list{
  border: 1px solid #ddd;
  border-radius: 5px;
  border-bottom: 1px solid #fff;
}
#activity .tips{
  margin-top: 5px;
}
#activity .list .up{
  position: absolute;
  top: 14px;
  left: 95%;
}
.g-rotate-bar{
  display: none;
}
.crop-box .g-resize{
  display: none;
}
.up-move {
  -webkit-transition: -webkit-transform .5s;
  transition: -webkit-transform .5s;
  transition: transform .5s;
  transition: transform .5s, -webkit-transform .5s;
  box-shadow:0 0 10px #eaeaea;
  border-top: 1px solid #eaeaea;
  border-bottom: 1px solid #eaeaea;
}



