
.activityitem{
  width: 100%;
}
.activityitem .act-header{
  height: 42px;
  border-bottom: 1px solid #dddddd;
}
.activityitem .act-header .error{
  color: #ff4949;
  font-size: 12px;
  line-height: 43px;
  padding-left: 10px;
}
.headerfocus{
  background-color: #f7f7f7;
}
.activityitem .act-header a{
  font-size: 13px;
  line-height: 42px;
}
.activityitem .act-header .el-input{
  margin-top: 5px;
  width: 150px;
  margin-left: 11px;
  float: left;
}
.activityitem .act-header .el-input .el-input__inner{
  height: 30px;
  font-size: 13px;
  font-family: "微软雅黑";
}
.activityitem .act-header .el-input .el-input__inner:hover{
  border-color: #99A9BF;
}
.activityitem .act-header .el-input .el-input__inner:focus{
  border-color: #99A9BF;
}
.activityitem .name{
  float: left;
  margin-left: 21px;
  color: #353638;
}
.activityitem .name:hover{
  text-decoration: underline;
}
.activityitem i{
  float: left;
  margin-left: 11px;
  font-size: 9px;
  line-height: 42px;
  color: #cdcdcd;
}
.activityitem .delete{
  float: right;
  color: #cacaca;
  position: absolute;
  left: 88%;
}
.activityitem .act-content{
  width: 100%;
  border-bottom: 1px solid #dddddd;
}
.activityitem .act-content>div{
  width: 637px;
  margin: 0 auto;
}
.activityitem .act-content .bottom{
  margin-top: 5px;
  height: 200px;
}
.activityitem .greenbutton{
  float: right;
  margin-top: 151px;
}
.expand-enter-active {
  max-height: 500px;
  overflow: hidden;
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}
.expand-enter{
  max-height: 0;
}
.expand-leave-active {
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
  max-height: 0px;
  overflow: hidden;
}
.expand-leave {
  max-height: 500px;
}
.activityitem .bottom .wrapper{
  width:260px;
  height:150px;
  margin-top:9px;
}
.activityitem .bottom .wrapper p{  
  line-height: 150px; 
  font-size: 21px;
}



