
.departmentitem{
  width: 100%;
}
.departmentitem .dep-header{
  height: 42px;
  border-bottom: 1px solid #dddddd;
}
.departmentitem .dep-header .error{
  color: #ff4949;
  font-size: 12px;
  line-height: 43px;
  padding-left: 10px;
}
.departmentitem .dep-header a{
  font-size: 13px;
  line-height: 42px;
}
.departmentitem .dep-header .el-input{
  margin-top: 5px;
  width: 150px;
  margin-left: 11px;
  float: left;
}
.departmentitem .dep-header .el-input .el-input__inner{
  height: 30px;
  font-size: 13px;
  font-family: "微软雅黑";
}
.departmentitem .dep-header .el-input .el-input__inner:hover{
  border-color: #99A9BF;
}
.departmentitem .dep-header .el-input .el-input__inner:focus{
  border-color: #99A9BF;
}
.headerfocus{
  background-color: #f7f7f7;
}
.departmentitem .dep-content{
  height: 298px;
  border-bottom: 1px solid #dddddd;
  margin-top: -1px;
}
.departmentitem .dep-content>div{
  float: left;
}
.departmentitem .tips{
  margin-left: 13px;
}
.departmentitem .greenbutton{
  margin-top: 14px;
  float: right;
}
.departmentitem .dep-header a{
  font-size: 13px;
  line-height: 42px;
}
.departmentitem .name{
  margin-left: 21px;
  color: #353638;
}
.departmentitem .name:hover{
  text-decoration: underline;
}
.departmentitem i{
  margin-left: 11px;
  font-size: 9px;
  line-height: 42px;
  color: #cdcdcd;
}
.departmentitem .delete{
  float: right;
  color: #cacaca;
  position: absolute;
  left: 88%;
}
.departmentitem .title{
  line-height: 40px;
  margin-left: 0px;
}
.departmentitem .formtextarea{
  width:510px;
  float:left;
  margin-left:18px;
  margin-top:7px;
}
.departmentitem .el-form-item__error{
  padding-left: 20px;
  line-height: 15px;
}
.departmentitem .dep-content .wrapper{
  width:114px;
  height:190px;
}
.departmentitem .dep-content .wrapper p{  
  line-height: 190px; 
  font-size: 20px;
}



