
#admin{
  width: 680px;
  float: left;
  margin-left: 40px;
  margin-top: 3px;
  margin-bottom: 40px;
}
#admin .admin-num{
  margin-bottom: 7px;
}
#admin .admin-num span{
  line-height: 40px;
}
#admin .table>div{
  border-top: 1px solid #eeeeee;
  height: 40px;
  line-height: 40px;
  font-size: 13px;
}
#admin .table .table-header{
  border-top: 2px solid #eeeeee;
  font-weight: bold;
}
#admin .table .table-content{
  color: #34383b;
}
#admin .table .name{
  margin-left: 10px;
  width: 220px;
  display: inline-block;
}
#admin .table .content{
  width: 240px;
  display: inline-block;
}
#admin .table .table-content .name{
  color: #5e7a88;
}
#admin .table .tips{
  margin-top: -2px;
  margin-left: 4px;
  border:none;
  font-size: 11px;
}
#admin .el-pagination{
  text-align: center;
  margin-top: 10px;
  margin-bottom: 5px;
}
#admin .el-pager li{
  border: none;
  font-size: 15px;
  min-width: 30px;
  height: 30px;
}
#admin .el-pagination button, .el-pagination span{
  font-size: 15px;
  min-width: 30px;
  height: 30px;
  line-height: 30px;
}
#admin .el-pagination .btn-next, .el-pagination .btn-prev{
  border: none;
}
#admin .el-pager li.active{
  background-color: #0175d8;
}
#admin .el-pager li.active:hover{
  color: #fff;
}
#admin .el-pager li:hover{
  color: #0175d8;
}
#admin .el-pagination button:hover{
  color: #0175d8;
}
.el-loading-mask {
  background-color: rgba(255,255,255,.4);
}



