
#login{
  width: 100%;
  min-width: 1170px;
}
#login .login-header{
  height: 87px;
  background-color: #000;
  color: #fff;
  font-family: "黑体";
}
#login .login-header div {
  margin-left: auto;
  margin-right: auto;
  width: 1170px;
  height: 100%;
}
#login .login-header p{
  line-height: 87px;
  font-size: 23px;
  margin-left: 36px;
  float: left;
}
#login .login-content{
  width: 1100px;
  margin: 0 auto;
  height: 500px;
  border-bottom: 1px solid #e5e5e5;
  margin-top: 15px;
}
#login .login-content .left{
  margin-top: 10px;
  float: left;
  padding-left: 106px;
}
#login .login-content .right{
  float: left;
  width: 380px;
  margin-top: 120px;
  border: 1px solid #e5e5e5;
  border-radius: 5px;
  height: 250px;
  margin-left: 120px;
}
#login .login-content .right p{
  border-bottom: 1px solid #e5e5e5;
  height: 45px;
  line-height: 45px;
  padding-left: 30px;
}
#login .el-form{
  padding-top: 20px;
  width: 310px;
  margin: 0 auto; 
  height: 100px;
}
#login .tips{
  width: 320px;
  margin: 0 auto;
}
#login .tips span{
  text-align: right;
  display: block;
  line-height: 40px;
}
#login .login-footer{
  margin-top: 10px;
  text-align: center;
  font-size: 14px;
  color: #ccc;
}
#login .login-footer a{
  color: #ccc;
  margin-left: 10px;
}
#login .login-content button{
  color: #fff;
  display: block;
  line-height: 29px;
  font-size: 14px;
  margin-left: 242px;
  height: 32px;
  width: 104px;
  border-radius: 3px;
  background-color: #0175d8;
  text-align: center;
  border: none;
}
#login .remark{
  color: #FF4949;
}



