
.navbar{
  width: 100%;
  min-width: 1170px;
  height: 87px;
  background-color: #000;
  color: #fff;
  font-family: "黑体";
}
.navbar .nav-content {
  margin-left: auto;
  margin-right: auto;
  width: 1170px;
  height: 100%;
}
.navbar .nav-content p{
  line-height: 87px;
  font-size: 23px;
  margin-left: 36px;
  float: left;
}
.navbar .nav-content a{
  color: #fff;
  text-decoration:none;
  font-size: 19px;
  line-height: 87px;
  float: right;
  margin-right: 30px;
}



