
#department{
  width: 680px;
  float: left;
  margin-left: 40px;
  margin-top: 3px;
  margin-bottom: 40px;
}
#department .dep-num{
  margin-bottom: 7px;
}
#department .dep-num span{
  line-height: 40px;
}
#department .list{
  border: 1px solid #ddd;
  border-radius: 5px;
  border-bottom: 1px solid #fff;
}
#department .tips{
  margin-top: 5px;
}
.el-dialog--small{
  width: 400px;
  border: 1px solid #ccc;
  border-radius: 3px;
}
.el-dialog__header{
  border-bottom: 1px solid #ccc;
  height: 50px;
  padding: 0;
  padding-left: 19px;
}
.el-dialog__header .el-dialog__title{
  font-size: 15px;
  font-weight: bold;
  line-height: 50px;
}
.el-dialog__body{
  padding-top: 24px;
  padding-bottom: 10px;
}
.el-dialog__footer{
  padding: 13px 20px 15px;
  height: 60px;
}
.el-dialog__footer button{
  width: 60px;
  height: 32px;
  border-radius: 3px;
  font-size: 12px;
  color: #393a3c;
  border: 1px solid #ccc;
  background-color: #fff;
  outline: none;
  cursor:pointer;
}
.el-dialog__footer .cancel{
  margin-right: 4px;
}
.el-dialog__footer .cancel:hover{
  color: #42af46;
  border-color: #42af46;
}
.el-dialog__footer .confirm{
  width: 104px;
  color: #fff;
  border-color: #42af46;
  background-color: #42af46;
}
.el-dialog__footer .confirm:hover{
  background-color: #5bb85d;
}
#department .list .up{
  position: absolute;
  top: 14px;
  left: 95%;
}



